import React, { Component, Suspense, lazy } from 'react';
import { Link, Route, Redirect } from "react-router-dom";
import { inject, observer } from "mobx-react";
import axios from 'axios';

import AdminNavbar from '../components/AdminNavbar';
import AdminSidebar from '../components/AdminSidebar';
import AdminFooter from '../components/AdminFooter';
import AnimatedSwitch from '../components/animated-switch';
import Loading from '../components/Loading';
import LoadingFailed from '../components/LoadingFailed';

const Dashboard = lazy(() => import('./Dashboard'));
const StudentPages = lazy(() => import('./StudentPages'));
const SetupPages = lazy(() => import('./SetupPages'));
const ExaminationPages = lazy(() => import('./ExaminationPages'));
const TeacherPages = lazy(() => import('./TeacherPages'));
const StaffPages = lazy(() => import('./StaffPages'));
const Member = lazy(() => import('./Member'));
const StockPages = lazy(() => import('./StockPages'));
const FinancePages = lazy(() => import('./FinancePages'));
const Timetable = lazy(() => import('./Timetable'));
const Visitors = lazy(() => import('./Visitors'));
const Message = lazy(() => import('./Message'));
const AccountPages = lazy(() => import('./AccountPages'));
const History = lazy(() => import('./History'));

@inject("userStore")
@observer
export default class Admin extends Component {
	logout = () => {
		axios.post('/auth/logout').then(res=>{
			this.props.userStore.setUser({email: null});
		})
	}

	render() {
		if(!this.props.userStore.user.email){
			return <Redirect to={{pathname: "/login", state: {redirect: this.props.location.pathname}}} />;
		}
		let match = this.props.match;
		return <div className="wrapper">
			<AdminSidebar match={match} />
			<div className="main-panel">
				<AdminNavbar logout={this.logout} />
				<div className="content">
					<LoadingFailed>
						<Suspense fallback={<Loading />} >
							<AnimatedSwitch>
								<Route path={`${match.path}/dashboard`}> <Dashboard/> </Route>
								<Route path={`${match.path}/students`} render={(props)=> <StudentPages {...props} /> } />
								<Route path={`${match.path}/setup`} render={(props)=> <SetupPages {...props} /> } />
								<Route path={`${match.path}/examination`} render={(props)=> <ExaminationPages {...props} /> } />
								<Route path={`${match.path}/teachers`} render={(props)=> <TeacherPages {...props} /> } />
								<Route path={`${match.path}/staffs`} render={(props)=> <StaffPages {...props} /> } />
								<Route path={`${match.path}/members`} render={(props)=> <Member {...props} /> } />
								<Route path={`${match.path}/stocks`} render={(props)=> <StockPages {...props} /> } />
								<Route path={`${match.path}/timetable`} render={(props)=> <Timetable {...props} /> } />
								<Route path={`${match.path}/finances`} render={(props)=> <FinancePages {...props} /> } />
								<Route path={`${match.path}/visitors`} render={(props)=> <Visitors {...props} /> } />
								<Route path={`${match.path}/messages`} render={(props)=> <Message {...props} /> } />
								<Route path={`${match.path}/account`} render={(props)=> <AccountPages {...props} /> } />
								<Route path={`${match.path}/history`} render={(props)=> <History {...props} /> } />
								<Route exact path={`${match.url}`}> <Redirect to={`${match.url}/dashboard`} /> </Route>
								<Route path=""><h1>404 Not Found</h1></Route>
							</AnimatedSwitch>
						</Suspense>
					</LoadingFailed>
				</div>
				<AdminFooter />
			</div>
		</div>
	}
}